import React from "react";
import { Link } from "react-router-dom";
import "../../css/hexvgon/mainHex.css";


function NavHex({style, release,member}) {

    return(
<div className={style}>
          <Link to={release}></Link>
          <Link to={member}> </Link>
          <Link to="/photo"></Link>
          <Link to="/news"></Link>
        </div>
    )
}

export default NavHex;